import { supabase } from '@/lib/supabaseClient';
import { withSupabaseHandler } from '@/api/utils';
//   
export var fetchFavorites = function fetchFavorites() {
    return withSupabaseHandler(supabase.from('Favorites').select('*'));
};
//  
export var addFavorite = function addFavorite(favoriteData) {
    return withSupabaseHandler(supabase.from('Favorites').insert([favoriteData]));
};
//  
export var deleteFavorite = function deleteFavorite(_ref) {
    var movieId = _ref.movieId, userId = _ref.userId;
    return withSupabaseHandler(supabase.from('Favorites')["delete"]().eq('movie_id', movieId).eq('user_id', userId));
};
